<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="40">
        <el-col :span="9">
          <el-input placeholder="" v-model="queryInfo.query" clearable
                    @clear="getUserList" @keyup.enter.native="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList">            
            </el-button>
          </el-input>                    
        </el-col>
        <el-col :span="4">
          <el-button class="adduser" type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" stripe border>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>

        <el-table-column label="">
          <template v-slot="scope">
            <el-switch v-model="scope.row.mg_state"
                      @change="userStateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>

        <el-table-column label="" width="200px" align="center">
          <template v-slot="scope">
            <!--  -->
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
            <!--  -->
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeUserById(scope.row.id)"></el-button>
            <!--  -->
            <el-tooltip content="" placement="top" :enterable="false">             
              <el-button type="warning" size="mini" icon="el-icon-setting" @click="roleSet(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>      
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    <!--  -->
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="600px"
      @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="120px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="600px"
      @close="editDialogClosed">
      <!--  -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="120px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="roleSetDialogVisible"
      width="600px"
      @close="roleSetDialogClosed">
      <!--  -->
      <div class="roleSetContent">
        <p>: &nbsp;&nbsp;&nbsp;{{userInfo.username}}</p>
        <p>: &nbsp;&nbsp;&nbsp;{{userInfo.role_name}}</p>
        <p>
          <el-select v-model="roleSetId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="roleSetDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleSet"> </el-button>
      </span>
    </el-dialog>
  <!--  -->
  </div>
</template>

<script>
export default {
  data() {
    // 
    var validateEmail = (rule, value, callback) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@+([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if(!regEmail.test(value)) {
        // 
        return callback(new Error(''))
      }
      // 
      callback()
    }
    // 
    var validateMobile = (rule, value, callback) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if(!regMobile.test(value)) {
        // 
        return callback(new Error(''))
      }
      // 
      callback()
    }

    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateMobile, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      //  
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateMobile, trigger: 'blur' }
        ]
      },
      //  
      roleSetDialogVisible: false,
      //  
      userInfo: {},
      //  
      rolesList: [],
      // id
      roleSetId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList() {
      const {data: res} = await this.$http.get('users', {
        params: this.queryInfo
      })
      if(res.meta.status !== 200){
        return this.$message.error({
          message: res.meta.msg,
          duration: 1000
        });
      }
      this.userlist = res.data.users
      this.total = res.data.total
      // console.log(res.data);
    },
    // 
    handleSizeChange(newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange(newPage) {
      // console.log(newPage);
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    async userStateChanged(userInfo) {
      // console.log(userInfo);
      const {data: res} = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      // console.log(res);
      if(res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error({
          message: res.meta.msg,
          duration: 1000
        })
      }
      this.$message.success({
        message: res.meta.msg,
        duration: 1000
      })
    },
    // 
    addDialogClosed() {
      // 
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser() {
      // 
      this.$refs.addFormRef.validate(async valid => {
        // console.log(valid);
        if(!valid) return
        // 
        const {data: res} = await this.$http.post('users', this.addForm)
        // console.log(res);
        if(res.meta.status !== 201) {
          this.$message.error({
            message: res.meta.msg,
            duration: 1000
          })
        }
        this.$message.success({
          message: res.meta.msg,
          duration: 1000
        })
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      // console.log(id);
      const {data: res} = await this.$http.get('users/' + id)
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '',
          duration: 1000
        })
      }
      this.editDialogVisible = true
      this.$nextTick(() => {
        this.editForm = res.data
      })
    },
    // 
    editDialogClosed() {
      // // 
      // this.$refs.editFormRef.resetFields()
      // 
      this.$refs.editFormRef.clearValidate()
    },
    // 
    editUserInfo() {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if(!valid) return
        // 
        const {data: res} = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email, mobile: this.editForm.mobile
        })
        // 
        if(res.meta.status !== 200) {
          return this.$message.error({
            message: '',
            duration: 1000
          })
        }
        // 
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success({
          message: '',
          duration: 1000
        })
      })
    },
    // id
    async removeUserById(id){ 
      // console.log(id);
      // 
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if(confirmRes !== 'confirm') {
        return this.$message({
          message: '',
          duration: 1000
        })
      }
      // 
      const {data: res} = await this.$http.delete('users/' + id)
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1000
        })
      }
      // 
      this.$message.success({
        message: '!',
        duration: 1000
      })
      this.getUserList()
    },
    // 
    async roleSet(userInfo) {
      // console.log(userInfo);
      this.userInfo = userInfo
      // 
      const {data: res} = await this.$http.get('roles')
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '',
          duration: 1000
        })
      }
      this.rolesList = res.data

      this.roleSetDialogVisible = true
    },
    // 
    async saveRoleSet() {
      const {data: res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid: this.roleSetId})
      if(res.meta.status !== 200) {
        return this.$message.error({
          message: '!',
          duration: 1000
        })
      }
      // console.log(res);
      this.$message.success({
        message: '!',
        duration: 1000
      })
      // ,
      this.getUserList()
      this.roleSetDialogVisible = false
    },
    // 
    roleSetDialogClosed() {
      this.roleSetId = '',
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
.adduser {
  padding-left: 15px;
  padding-right: 13px;
  font-size: 14px;
  letter-spacing: .8px;
}
.el-input {
  width: 350px;
}
.roleSetContent {
 margin-left: 78px;
}
</style>